#aboutme {
  color: #3d4451;
}

#more-info,
#aboutme .card {
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
}

#aboutme .card {
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.12), 0 1px 4px rgba(0, 0, 0, 0.24);
}

#aboutme .profile-preword {
  float: left;
  margin-bottom: 10px;
  background-color: #e8676b;
}

#aboutme .profile-preword span {
  color: #fff;
  font-size: 1rem;
  font-weight: 700;
  line-height: 1.1;
  display: inline-block;
  padding: 7px 12px;
  text-transform: uppercase;
  position: relative;
}

#aboutme .profile-preword span:before {
  content: '';
  width: 0;
  height: 0;
  top: 100%;
  left: 5px;
  display: block;
  position: absolute;
  border-style: solid;
  border-width: 0 0 12px 12px;
  border-color: transparent;
  border-left-color: #e8676b;
}

#aboutme .profile-title {
  line-height: 1.1;
  margin-bottom: 5px;
}
#aboutme .profile-title strong {
  font-weight: 700;
}

#aboutme #more-info {
  font-size: 0.8rem !important;
}

#social-links {
  background-color: #e8676b;
  min-height: 5rem;
  font-size: 1.4rem;
}

#social-links a {
  background: #e8676b;
  height: 3rem;
  width: 3rem;
  border-radius: 50%;
  transition: all 0.5s ease;
}

#social-links a:hover {
  background: #d05c60;
}

#more-info li,
#more-info p {
  font-size: 1.3rem;
}
