#navbarNav ul li.nav-item {
  display: inline-block;
  font-size: 13px;
  font-weight: 700;
  line-height: 1;
  text-transform: uppercase;
  display: block;
  padding: 12px 12px;
}
#navbarNav ul li.nav-item a {
  position: relative;
}

.nav-link {
  cursor: pointer;
}

.collapsing,
.collapse.show {
  background-color: #e8676b;
  border-radius: 2rem;
  color: #212529;
}

.collapsing a,
.collapse.show a {
  background-color: #e8676b;
  border-radius: 2rem;
  color: #212529;
}

#navbarNav.collapsing ul li.nav-item a::after,
#navbarNav.collapse.show ul li.nav-item a::after {
  background-color: #f7f7f7;
}

#navbarNav ul li.nav-item a::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 0.2rem;
  border-radius: 5rem;
  background-color: #e8676b;
  transition: width 0.2s ease-out;
}

#navbarNav ul li.nav-item a::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 0.2rem;
  border-radius: 5rem;
  background-color: #e8676b;
  transition: width 0.2s ease-out;
}

#navbarNav ul li.nav-item a.active::after,
#navbarNav ul li.nav-item a:hover::after {
  width: 100%;
}

#navbarNav > ul > li.nav-item.dropdown ul li div {
  cursor: pointer;
}

.dropdown-item.active,
.dropdown-item:active {
  background-color: #e8676b;
}

header nav {
  background-color: transparent;
  transition: background-color 0.5s ease;
}

header .shadow {
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.19), 0 6px 10px rgba(0, 0, 0, 0.23) !important;
}
